import React, { useMemo } from "react";
import { Col, Row } from "react-bootstrap";

// needed asssets

// svgs
import { ReactComponent as Search } from "assets/svgs/search.svg";

// images

// needed styles
import styles from "./index.module.scss";
import { FormattedMessage, useIntl } from "react-intl";
import { useDispatch, useSelector } from "react-redux";
import { searchCV } from "api/employee";
import { useNavigate } from "react-router-dom";
import { fetchCVs } from "store/empolyee/slice";

const SearchEmployees = () => {
  const intl = useIntl();
  const [search, setSearch] = React.useState({});
  const {
    meta: { locale },
    authentication: { user, isLoggedIn },
    empolyee: { specialties, nationalties },
  } = useSelector((state) => state);
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const specialtiesOptions = useMemo(
    () => [
      ...specialties.map((specialty) => ({
        label: locale === "ar" ? specialty.name_ar : specialty.name_en,
        value: specialty._id,
      })),
    ],
    [specialties, locale]
  );
  const getCVs = async () => {
    dispatch(fetchCVs({ ...search, callBack: () => navigate("/cvs") }));
  };
  return (
    <>
      <div className={styles["wrapper"]} method="post" action="/">
        <Row>
          <h3 className={styles["header"]}>
            <FormattedMessage id="lookForEmployee" />
          </h3>
        </Row>
        <Row>
          <Col lg={6}>
            <div className={`${styles["search"]}`}>
              <select
                type="search"
                name="search"
                onChange={(e) =>
                  setSearch({ ...search, specialty_id: e.target.value })
                }
              >
                <option value={null} selected={true}>
                  {/* ابحث عن المهنه */}
                  {intl.formatMessage({ id: "lookForEmployee" })}
                  {/* <FormattedMessage id="lookForEmployee" /> */}
                </option>
                {specialtiesOptions.map((specialty) => (
                  <option value={specialty.value}>{specialty.label}</option>
                ))}
              </select>
              {/* <Search /> */}
            </div>
          </Col>
          <Col lg={6}>
            <div className={`${styles["select"]}`}>
              <select
                type="search"
                onChange={(e) =>
                  setSearch({ ...search, country_id: e.target.value })
                }
              >
                <option value="">اختر الدوله</option>
                {nationalties.map((nationalty) => (
                  <option key={nationalty._id} value={nationalty._id}>
                    {locale === "ar"
                      ? nationalty?.["name_ar"]
                      : nationalty?.["name_en"]}
                  </option>
                ))}
              </select>
            </div>
          </Col>
        </Row>
        <Row className="justify-content-center">
          <button
            className={styles["button"]}
            onClick={
              user?.search_type_id && isLoggedIn
                ? getCVs
                : () => navigate("/employmentSubscription")
            }
          >
            {user?.search_type_id && isLoggedIn ? (
              <FormattedMessage id="search" />
            ) : (
              <FormattedMessage id="subscribeFirst" />
            )}
          </button>
        </Row>
      </div>
    </>
  );
};

export default SearchEmployees;
