import { getAllHeroBanners } from "api/home";
import React, { useEffect, useState } from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import { v4 } from "uuid";

// needed asssets

const HeroSlider = () => {
  const [slides, setSlides] = useState([]);
  useEffect(() => {
    async function fetchData() {
      // You can await here
      const response = await getAllHeroBanners();
      const images = response.map((data) => data?.picture);
      setSlides(images);
      // ...
    }
    fetchData();
  }, []);

  return (
    //
    <div className={"single-umrah-slider"}>
      <Swiper
        slidesPerView={1}
        loop={true}
        navigation={true}
        autoplay={{
          delay: 2000,
          disableOnInteraction: false,
        }}
        className="mySwiper"
      >
        {slides?.map((slide) => (
          <SwiperSlide key={v4()}>
            <div>
              <img
                objectFit="cover"
                src={slide}
                alt="hero-img"
                width={"100%"}
                height={"650px"}
                style={{ objectFit: "cover" }}
              />
            </div>
          </SwiperSlide>
        ))}
      </Swiper>
    </div>
  );
};

export default HeroSlider;
