import { getMostSearchTrips } from "api/home";
import DefaultCard from "components/shared/DefaultCard/DefaultCard";
import SpecialCard from "components/shared/SpecialCard/SpecialCard";
import React, { useEffect, useState } from "react";
import { Col } from "react-bootstrap";
import { Swiper, SwiperSlide } from "swiper/react";
import { v4 } from "uuid";

// needed asssets

// svgs
// import { ReactComponent as Search } from "assets/svgs/search.svg";

// images

// needed styles
// import styles from "./index.module.scss";

const MostSearchTripsSlider = ({ data }) => {
  let numberOfSlides = null;
  if (data.length <= 0) return null;

  switch (data?.length) {
    case 2:
      numberOfSlides = 2;
      break;
    case 1:
      numberOfSlides = 1;
      break;

    default:
      numberOfSlides = 3;
      break;
  }

  return (
    <div className={`home-slider`}>
      <Swiper
        // onSwiper={(swiper) => console.log(swiper)}
        slidesPerView={numberOfSlides}
        spaceBetween={45}
        slidesPerGroup={3}
        // loop={true}
        loopFillGroupWithBlank={true}
        pagination={{
          clickable: true,
        }}
        // navigation={true}
        autoplay={{
          delay: 2000,
          disableOnInteraction: false,
        }}
        className="mySwiper"
        breakpoints={{
          320: {
            slidesPerView: numberOfSlides >= 3 ? 1 : numberOfSlides,
            spaceBetween: 20,
          },
          630: {
            slidesPerView: numberOfSlides >= 3 ? 2 : numberOfSlides,
            spaceBetween: 20,
          },
          992: {
            slidesPerView: numberOfSlides >= 3 ? 3 : numberOfSlides,
            spaceBetween: 30,
          },
        }}
      >
        {data?.map((trip) => {
          const {
            title_ar,
            price_per_person,
            _id: tripId,
            pictures,
          } = trip || {};
          return (
            <SwiperSlide key={v4()}>
              <SpecialCard
                data={trip}
                withButton
                header={title_ar}
                // subHeader={itinerary_title_ar}
                title={price_per_person}
                image={pictures[0]}
                tripId={tripId}
              />
            </SwiperSlide>
          );
        })}
      </Swiper>
    </div>
  );
};

export default MostSearchTripsSlider;
